import { useState, useEffect, Suspense, lazy } from "react";
import { Routes, Route } from "react-router-dom";
import Layout from "./Layout";
import LoginPage from "./components/Auth/LoginPage";
import ErrorBoundary from "./components/Utils/ErrorBoundary";
import { onAuthStateChange } from "./services/firebase";
import { logger } from "./utils/logger";

// Lazy load pages for better code splitting
const Dashboard = lazy(() => import("./pages/Dashboard"));
const SalesEntry = lazy(() => import("./pages/SalesEntry"));
const Notes = lazy(() => import("./pages/Notes"));

// Admin pages - loaded separately for better chunking
const UserManagement = lazy(() => import("./pages/UserManagement"));
const AuditLogs = lazy(() => import("./pages/AuditLogs"));
const Settings = lazy(() => import("./pages/Settings"));

// Development utilities
const DevUtils = lazy(() => import("./pages/DevUtils"));

// Loading component
const PageLoader = () => (
  <div className="min-h-screen bg-[#0e0e0e] flex items-center justify-center">
    <div className="text-center">
      <div className="w-8 h-8 border-2 border-[#bc9a64] border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
      <p className="text-gray-400">Loading...</p>
    </div>
  </div>
);

function App() {
  const [user, setUser] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const unsubscribe = onAuthStateChange((firebaseUser) => {
      setUser(firebaseUser);
      setLoading(false);
    });

    return () => unsubscribe();
  }, []);

  // Log app initialization
  useEffect(() => {
    logger.info("OnSale Analytics App initialized");
  }, []);

  if (loading) {
    return (
      <div className="min-h-screen bg-[#0e0e0e] flex items-center justify-center">
        <div className="w-8 h-8 border-2 border-[#bc9a64] border-t-transparent rounded-full animate-spin"></div>
      </div>
    );
  }

  if (!user) {
    return <LoginPage onLoginSuccess={() => setUser(true)} />;
  }

  return (
    <ErrorBoundary>
      <Suspense fallback={<PageLoader />}>
        <Routes>
          <Route
            path="/"
            element={
              <Layout>
                <Dashboard />
              </Layout>
            }
          />
          <Route
            path="/dashboard"
            element={
              <Layout>
                <Dashboard />
              </Layout>
            }
          />
          <Route
            path="/sales-entry"
            element={
              <Layout>
                <SalesEntry />
              </Layout>
            }
          />
          <Route
            path="/notes"
            element={
              <Layout>
                <Notes />
              </Layout>
            }
          />
          <Route
            path="/user-management"
            element={
              <Layout>
                <UserManagement />
              </Layout>
            }
          />
          <Route
            path="/audit-logs"
            element={
              <Layout>
                <AuditLogs />
              </Layout>
            }
          />
          <Route
            path="/settings"
            element={
              <Layout>
                <Settings />
              </Layout>
            }
          />
          <Route
            path="/dev-utils"
            element={
              <Layout>
                <DevUtils />
              </Layout>
            }
          />
        </Routes>
      </Suspense>
    </ErrorBoundary>
  );
}

export default App;
